import Vue from 'vue';
import {
	mapState
} from 'vuex';
import QQMapWX from '../../js_sdk/qqmap-wx-jssdk1.2/qqmap-wx-jssdk.js';
let qqmapsdk = new QQMapWX({
	key: 'QL7BZ-ZCJKK-72IJS-A6NA6-HRJ3F-ZYB6J'
});
export default {
	data() {
		return {
			currentNav: 0,
			currentTab: 1,
			newsInfo: [],
			attentionInfo: [],
			articleInfo: [],
			activityInfo: [],
			recruitInfo: [],
