							break;
						case 6:
							this.articleInfo[index].display_time = '星期六 ' + time[1];
							break;
					}
				} else {
					this.articleInfo[index].display_time = this.articleInfo[index].article_created;
				}
			}
		},
		formatActivityTime(index) {
			let time = this.activityInfo[index].activity_created.split(' ');
